body,html {
  margin: 0;
  font-family: 'Manrope', sans-serif;
}
.content-wrapper {
  padding: 40px 160px;
}
.header {
  background: url(./static/stadium.png);
  background-size: contain;
  height: 100vh;
  width: 100vw;
  overflow: hidden;
  background-position: center;
  background-attachment: fixed;
  background-repeat: no-repeat;
  position: relative;
}
.header img.hand {
  position: absolute;
  bottom: 0;
  left: calc((100vw - 85vh) / 2);
}

.header .title {
  position: absolute;
}
.header .title h1 {
  color: #ff4a3a;
  margin: 0;
  font-size: 32px;
  font-weight: bolder;
  background: #ffffff97;
  /* display: inline-block; */
}
.header .title span {
  font-size: 24px;
  font-weight: bolder;
  color: #1e1e63;
  background: #ffffff97;
}

.header .title img {
  width: 64px;
}

@media(min-width: 700px) {
  .header img.hand{
    height: 100%;
  }
  .header .title {
    bottom: 50px;
    left: 50px;
  }
}
@media(max-width: 700px) {
  .header {
    background-size: contain;
  }
  .header img.hand {
    width: 120vw;
    left: -5vw
  }
  .header .title {
    top: 50px;
    left: 50px;
  }
}


.game {
  padding: 120px 0;
  background: linear-gradient(45deg, #010070, #00003f);
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-position: left center;
  /* background-size: 50% auto; */
}

.game .content-wrapper {
  display: flex;
  align-items: center;
  justify-content: center;
  max-width: 1200px;
  margin: auto;
}

.game .instruction_wrapper {
  display: flex;
  align-items: center;
  justify-content: space-around;
  margin-bottom: -40px;
}

.game .instruction_wrapper:nth-child(2n) {
  flex-direction: row-reverse;
}

.game .instruction {
  padding: 24px;
  margin: 20px 0;
  width: 50%;
  font-size: 30px;
  font-weight: lighter;
}

.game .instruction {
  color: rgba(255,255,255,0.5);
  cursor: pointer;
}

.game .instruction b {
  font-size: 34px;
  color: #fff;
}

.game img {
  max-height: 60vh;
  border-radius: 40px;
  border: 12px solid #000;
}

@media(max-width: 1024px) {
  .content-wrapper {
    padding: 60px;
  }
  .game .instruction b {
    font-size: 28px;
  }
  .game .instruction {
    font-size: 24px;
  }
}

@media(max-width: 720px) {
  .game .instruction_wrapper,.game .instruction_wrapper:nth-child(2n) {
    flex-direction: column;
    text-align: center;
    margin-bottom: 0;
  }
  .game .instruction_wrapper .instruction {
    width: 100%;
  }
}

.footer {
  background: #05042f;
  text-align: center;
  color: #ffffff70;
  padding: 32px 20px;
}

.footer b {
  color: #fff;
}